import request from '@/utils/request';

// 获取商户的产品列表
export const getMerchantProducts = (merchantId: number) => {
  return request({
    url: `/api/v1/admin/merchant/${merchantId}/products`,
    method: 'get'
  });
};

// 添加商户
export const addMerchant = (api: string, data: Record<string, any> = {}) => {
  return request({
    url: api,
    method: 'post',
    data: data
  });
};

// 编辑商户
export const editMerchant = (api: string, data: Record<string, any> = {}) => {
  return request({
    url: api,
    method: 'post',
    data: data
  });
};

// 获取商户详情
export const getMerchantDetail = (api: string, id: number) => {
  return request({
    url: `${api}/${id}`,
    method: 'get'
  });
};